<template lang="html">
  <el-dialog :visible.sync="dialogVisible" title="" width="400px">
    <el-form :model="data" label-width="100px" size="small">
      <el-form-item label="">
        <el-input value="115" disabled />
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="data.drawno" type="number" placeholder="" />
      </el-form-item>
    </el-form>
    <span slot="footer">
      <el-button @click="confirmCancelOrder" type="primary" size="small"></el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  name: 'ResultsElevenDialogCancel',
  props: {
    data: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      dialogVisible: false
    }
  },
  methods: {
    toggleDialogVisible (status) {
      this.dialogVisible = status
    },
    confirmCancelOrder () {
      this.$httpAPI.cancelLotteryResultEleven({
        params: this.data
      }).then(response => {
        if (response.data.status === -100) {
          this.$message.warning(response.data.msg)
        } else if (response.data.status === 200) {
          this.$message.success('')
        }

        this.dialogCancelVisible = false
      }).catch(error => console.log(error))
    }
  }
}
</script>
